.btn {
  display: inline-flex;
  text-align: center;
  justify-content: center;
  color: #896e4a;
  padding: 12px 33px;
  background: transparent;
  cursor: pointer;
  outline: none;
  position: relative;
  font-size: 16px;
  transition: all 0.3s linear 0s;
  text-decoration: none;
  outline: none;
  position: relative !important;
  z-index: 1;
}

@media (min-width: 768px) {
  .btn {
    padding: 17px 33px;
  }
}

.btn:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: inherit;
  z-index: -1;
  box-shadow: inset 0 0 0 2px #896e4a;
  transition: box-shadow 0.3s, transform 0.3s;
}

@media (min-width: 768px) {
  .btn:before {
    transform: scale3d(1, 0.8, 1);
  }
}

.btn:hover:before, .btn:focus:before {
  transform: scale3d(1, 1, 1);
}

.btn.more-btn {
  padding: 12px 50px;
}

@media (min-width: 768px) {
  .btn.more-btn {
    padding: 18px 50px;
  }
}

.btn.white-btn {
  color: #000000;
}

.btn.white-btn:before {
  background: #fff;
  box-shadow: none;
}

.btn.filled-brown {
  color: #fff;
}

.btn.filled-brown:before {
  background: #896e4a;
}

.btn input.btn,
.btn button.btn {
  display: inline-block;
}

html {
  box-sizing: border-box;
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: transparent;
}

*,
*::before,
*::after {
